.slogan {
    height: 50vh;
    background-color: #252527;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
}
.container {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
}
.title {
    text-transform:  capitalize;
    letter-spacing: 0.2vw;
    font-size: 36px;
    padding-top: 20px;
}
.line {
    background-color: #40FF00;
    width: 55px;
    height: 1px;
    margin: 30px auto;
}

.btnShow {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    border: none;
    margin-top: 15px;
    padding: 10px 30px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 14px;
    outline: none;
    text-decoration: none;
    position: relative;
    line-height: 1;
    color: white;
    bottom: 0;
    width: 180px;
    height: 50px;
    background-color: transparent;
}
.btnShow a {
    text-decoration: none;
    color: white;
    width: 100%;
    height: 100%;
}
.btnShow:after {
    position: absolute;
    content: '';
    width: 120px;
    height: 8px;
    background-color: #40FF00;
    bottom: 0;
    z-index: -1;
    transition: all 0.6s ease-in-out;
}
.btnShow:hover a{
    color: white;
}
.btnShow:hover:after {
    width: 100%;
    height: 100%;
}



